<template>
    <section v-if="toys" class="toy-list">
        <ul class="clean list flex wrap space-between align-center">
            <toy-preview
                v-for="toy in toys"
                :key="toy._id"
                :toy="toy"
                @deleteToy="emitDelete"
            />
        </ul>
    </section>
</template>


<script>
import toyPreview from "../components/toy-preview.vue";

export default {
    name: "toy-list",
    props: {
        toys: Array,
    },
    methods: {
        emitDelete(toyId) {
            this.$emit("deleteToy", toyId);
        },
    },
    components: {
        toyPreview,
    },
};
</script>
