<template>
    <section class="toy-app">
        <div class="actions flex space-between align-center">
            <toy-filter></toy-filter>
            <button class="add-toy-btn action" @click="addToy">Add Toy</button>
        </div>
        <toy-list v-if="toys.length" :toys="toys" @deleteToy="deleteToy" />
        <div v-else class="loading flex justify-center">
            <img src="https://i.gifer.com/4V0b.gif" alt="" />
        </div>
        <pop-up :isShowen="isPopUpShowen" :closePopUp="togglePopUp">
            <h1 slot="header">Header slot title</h1>
            <chat-app />
            <h3 slot="footer">Footer slot title</h3>
        </pop-up>
        <button class="chat-btn" @click="togglePopUp">
            <i class="fas fa-comment-dots chat-icon"></i>
        </button>
        <!-- <user-msg></user-msg> -->
    </section>
</template>


<script>
import toyList from "../components/toy-list.vue";
import toyFilter from "../components/toy-filter.vue";
import PopUp from "../components/pop-up.vue";
import ChatApp from "../components/chat-app.vue";

export default {
    name: "toy-app",
    methods: {
        deleteToy(toyId) {
            this.$store.dispatch({
                type: "deleteToy",
                toyId,
            });
        },
        addToy() {
            this.$router.push("/edit");
        },
        togglePopUp() {
            this.$store.dispatch({
                type: "togglePopUp",
            });
        },
    },
    computed: {
        toys() {
            return this.$store.getters.toys;
        },
        filter() {
            return this.$store.getters.filterBy;
        },
        isPopUpShowen() {
            return this.$store.getters.isPopUpShowen;
        },
    },
    created() {
        this.$store.dispatch({
            type: "loadToys",
        });
    },
    components: {
        toyList,
        toyFilter,
        PopUp,
        ChatApp,
    },
};
</script>
