<template>
    <section class="chat-app">
        <ul class="msgs clean-list">
            <li class="msg-preview" v-for="(msg, idx) in msgs" :key="idx">
                {{ msg }}
            </li>
        </ul>
        <div class="chat-controls flex justify-center">
            <input
                class="chat-input"
                @keyup.enter="sendMsg"
                type="text"
                v-model="msg"
            />
            <button @click="sendMsg">Send</button>
        </div>
    </section>
</template>

<script>
export default {
    data() {
        return {
            msgs: [],
            msg: "",
        };
    },
    methods: {
        sendMsg() {
            this.msgs.push(this.msg);
            this.msg = "";
        },
    },
};
</script>

