<template>
    <section
        v-if="toy"
        class="toy-details flex column justify-center align-center"
    >
        <h2>{{ toy.name }}</h2>
        <h3>Price: {{ toy.price }}</h3>
        <h3>Type: {{ toy.type }}</h3>
        <h3 class="inStock" v-if="toy.inStock">In stock</h3>
        <h3 class="outOfStock" v-else>Out of stock</h3>
        <button type="button" @click="goBack">Back</button>

    </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";

export default {
    data() {
        return {
            toy: null,
        };
    },
    methods: {
        goBack() {
            this.$router.push("/toys");
        },
    },
    created() {
        const toyId = this.$route.params.id;
        toyService.getById(toyId).then((toy) => (this.toy = toy));
    },
};
</script>

