import axios from 'axios';

export const toyService = {
    query,
    getById,
    remove,
    save,
    getEmpty
}

// const BASE_URL = 'http://localhost:3000'

const BASE_URL = (process.env.NODE_ENV !== 'development') ? '/api/toy' : '//localhost:3000/api/toy';



function query(filterBy = {}) {
    let qst = '';
    if (filterBy) {
        qst = getQueryStrPrms(filterBy)
    }
    let path = `${BASE_URL}/${qst}`;
    console.log('path:', path);
    return axios.get(path)
        .then(res => {
            return res.data
        })
}

function getQueryStrPrms(filterBy) {
    let str = '?'
    if (filterBy.sortBy) {
        str += `_sort=${filterBy.sortBy}&`
    }
    if (filterBy.inStock) {
        str += `inStock=${filterBy.inStock}&`
    }
    if (filterBy.type) {
        str += `type=${filterBy.type}`
    }
    console.log("str", str)
    return str
}

function getById(id) {
    console.log('id in getById:', id);
    return axios.get(`${BASE_URL}/${id}`)
        .then(res => {
            console.log('res.data', res.data);
            return res.data
        })
}

function remove(id) {
    return axios.delete(`${BASE_URL}/${id}`)
        .then(res => res.data)
}

function save(toy) {
    const savedToy = (toy._id) ? _update(toy) : _add(toy)
    return savedToy;

}

function _add(toy) {
    return axios.post(`${BASE_URL}`, toy)
        .then(res => res.data)

}

function _update(toy) {
    console.log('update in front service:', toy);
    return axios.put(`${BASE_URL}/${toy._id}`, toy)
        .then(res => res.data)

}

function getEmpty() {
    return {
        name: '',
        price: 0,
        type: '',
        createdAt: new Date(),
        inStock: false
    }
}