<template>
    <form @change="setFilter" class="toy-filter">
        <select class="action" v-model="filterBy.type">
            <option value="all">All</option>
            <option v-for="(type, idx) in types" :key="idx" :value="type">
                {{ type }}
            </option>
        </select>
        <select class="action" v-model="filterBy.sortBy">
            <option value="name">Name</option>
            <option value="price">Price</option>
        </select>
        <label class="checkbox action"
            >In Stock?
            <input type="checkbox" v-model="filterBy.inStock" />
        </label>
    </form>
</template>

<script>
export default {
    data() {
        return {
            filterBy: {
                type: "all",
                inStock: false,
                sortBy: "name",
            },
        };
    },
    computed: {
        types() {
            console.log("types:", this.$store.getters.types);
            return this.$store.getters.types;
        },
    },
    methods: {
        setFilter() {
            let filterCopy = JSON.parse(JSON.stringify(this.filterBy));
            if (filterCopy.type === "all") delete filterCopy.type;
            this.$store.dispatch({
                type: "setFilterBy",
                filterBy: filterCopy,
            });
        },
    },
    created() {
        this.setFilter();
    },
};
</script>

