<template>
    <li
        @click.stop="toyDetails(toy._id)"
        class="toy-preview flex column justify-center align-center"
    >
        <h2>{{ toy.name }}</h2>
        <h3>{{ toy.type }}</h3>
        <h3>{{ toy.price }}$</h3>
        <h3 class="inStock" v-if="toy.inStock">In stock</h3>
        <h3 class="outOfStock" v-else>Out of stock</h3>

        <span class="toy-btns">
            <button @click.stop="editToy"><i class="fas fa-edit fa-icon"></i></button>
            <button @click.stop="emitDelete(toy._id)"><i class="fas fa-trash-alt fa-icon"></i></button>
            <!-- <button @click.stop="toyDetails(toy._id)">Details</button> -->
        </span>
    </li>
</template>


<script>
export default {
    name: "toy-preview",
    props: {
        toy: Object,
    },
    methods: {
        emitDelete(toyId) {
            this.$emit("deleteToy", toyId);
        },
        toyDetails(toyId) {
            this.$router.push(`/details/${toyId}`);
        },
        editToy() {
            this.$router.push(`/edit/${this.toy._id}`);
        },
    },
};
</script>
