import Vue from 'vue'
import Vuex from 'vuex'
import { toyStore } from './toy.store.js'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: true,
    state: {
        isPopUpShowen: false
    },
    getters: {
        isPopUpShowen(state) {
            return state.isPopUpShowen
        }
    },
    mutations: {
        togglePopUp(state) {
            state.isPopUpShowen = !state.isPopUpShowen
        }
    },
    actions: {
        togglePopUp({ commit }) {
            commit({ type: 'togglePopUp' })
        }
    },
    modules: {
        toyStore,
    }
})